.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #0a1943 0%, #1a2a5a 50%, #2a3a6a 100%);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.test-container {
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  margin-top: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.test-container h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #4CAF50;
}

.test-container p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.connection-status {
  padding: 0.5rem 1rem;
  border-radius: 25px;
  font-weight: bold;
  margin: 1rem 0;
  display: inline-block;
}

.connection-status.connected {
  background: rgba(76, 175, 80, 0.2);
  border: 1px solid #4CAF50;
  color: #4CAF50;
}

.connection-status.disconnected {
  background: rgba(244, 67, 54, 0.2);
  border: 1px solid #f44336;
  color: #f44336;
}

.controls-row {
  margin: 1.5rem 0;
}

.toggle-3d-btn {
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.3);
}

.toggle-3d-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(76, 175, 80, 0.4);
}

.toggle-3d-btn:active {
  transform: translateY(0);
}

.error-message {
  background: rgba(244, 67, 54, 0.1);
  border: 1px solid #f44336;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem 0;
  color: #f44336;
}

.error-message h3 {
  margin-top: 0;
  color: #f44336;
}

.error-fallback {
  background: rgba(255, 193, 7, 0.1);
  border: 1px solid #ffc107;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem 0;
  color: #ffc107;
  text-align: center;
}

.test-info {
  text-align: left;
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 10px;
  margin-top: 2rem;
}

.test-info h3 {
  color: #FFD700;
  margin-bottom: 1rem;
}

.test-info ul {
  list-style: none;
  padding: 0;
}

.test-info li {
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.test-info li:last-child {
  border-bottom: none;
}

.simple-data {
  margin-top: 2rem;
  text-align: left;
}

.simple-data h3 {
  color: #FFD700;
  margin-bottom: 1rem;
}

.whale-list {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 1rem;
}

.whale-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  margin: 0.5rem 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  border-left: 4px solid #4CAF50;
}

.whale-value {
  font-weight: bold;
  color: #4CAF50;
  font-size: 1.1rem;
}

.whale-time {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

.loading-3d {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  z-index: 1000;
}

.canvas-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.canvas-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.canvas-placeholder {
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 3rem;
  border-radius: 15px;
  text-align: center;
  max-width: 400px;
}

.canvas-placeholder h3 {
  color: #4CAF50;
  margin-bottom: 1rem;
}

/* Original styles for when we restore the full app */
.ui-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  pointer-events: none;
}

.ui-overlay > * {
  pointer-events: auto;
}

.header {
  text-align: center;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

.header h1 {
  margin: 0;
  font-size: 3rem;
  color: #4CAF50;
}

.header p {
  margin: 0.5rem 0;
  font-size: 1.2rem;
  opacity: 0.9;
}

.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 2rem;
  height: calc(100vh - 200px);
}

.left-panel, .right-panel {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.info-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 1.5rem;
  color: white;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.info-card h3 {
  margin-top: 0;
  color: #FFD700;
}

.info-card ul {
  list-style: none;
  padding: 0;
}

.info-card li {
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.info-card li:last-child {
  border-bottom: none;
}

/* Responsive design */
@media (max-width: 1200px) {
  .content-grid {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  
  .right-panel {
    flex-direction: row;
    overflow-x: auto;
  }
  
  .info-card {
    min-width: 300px;
  }
}

@media (max-width: 768px) {
  .header h1 {
    font-size: 2rem;
  }
  
  .content-grid {
    padding: 1rem;
  }
  
  .right-panel {
    flex-direction: column;
  }
}
